.dark {
    background-color: rgb(255, 184, 184);
  }
  
  .light {
    background-color: rgb(207, 207, 254);
  }

  .card{
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 10px;
      border-radius: 8px;
    }
    
    .card h4{
      margin-bottom: 0;
    }

  .card img {
    width: 180px;
    height: 180px;
    object-fit: cover;
  }